const now = new Date();
const howManyDaysToShow = 7;
const previousDays = getPreviousDays(howManyDaysToShow);


const labels = previousDays.map(el => el.toLocaleString('fr-FR', {  weekday: 'long' }));

function getPreviousDays() {
    const days = [];
    for (let i = 0; i < howManyDaysToShow; i++) {
        days.push(new Date(now.setDate(now.getDate() - 1)));
    }
    return days;
}

const data = [{x: 'Jan', net: 100, cogs: 50, gm: 50}, {x: 'Feb', net: 120, cogs: 55, gm: 75}];
const config = {
    type: 'bar',
    data: {
        labels: ['Jan', 'Feb'],
        datasets: [{
            label: 'Net sales',
            data: data,
            parsing: {
                yAxisKey: 'net'
            }
        }, {
            label: 'Cost of goods sold',
            data: data,
            parsing: {
                yAxisKey: 'cogs'
            }
        }, {
            label: 'Gross margin',
            data: data,
            parsing: {
                yAxisKey: 'gm'
            }
        }]
    },
};


